<script>
    import spinner from "../components/spinner";

    export default {
        name: "test4",
        components: {
            spinner
        },

        data() {
            return {
                show: false,
                height: 0,
                arr: ['ali', 'reza', 'mahdi', 'dnfdmmfn', 'kljlelwkl', 'ooopppp']
            }
        },

