        methods: {
            showHide() {
                const vm = this;
                if (!vm.show) {
                    vm.show = true;
                    document.getElementById('parent').style.height = document.getElementById('items').offsetHeight + 'px';
                    window.console.log(document.getElementById('items').offsetHeight);
                } else {
